* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

:root {
  --happy-mustard: #FEC155;
  --deep-purple: #1F1E20;
  --off-white: #F0EEE9;
  --white: #fff;
  --subtext: #333;
  --dots-grey: #E0E0E0;
  --happy-mustard-text: #1F1E20;
}

@media (prefers-color-scheme: dark) {
  :root {
    --happy-mustard: #FEC155;
    --deep-purple: #FFF;
    --off-white: #000;
    --white: #1F1E20;
    --subtext: #eaeaea;
    --dots-grey: #0D0D0D;
    --happy-mustard-text: #1F1E20;
  }
}

@media (prefers-reduced-motion) {
  * {
    transition: 0s !important;
  }
}


@media screen and (min-width: 960px) {
  html {
    overflow-y: scroll;
  }
}

body, * {
  margin: 0;
  font-family: Palanquin, Verdana, Geneva, Tahoma, sans-serif;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.5;
  color: var(--deep-purple);
  scroll-behavior: smooth;
}

/* *:focus {
  box-shadow: 0 0 0 4px black;
  outline: 4px solid blue;
} */

/* The default outline styling, for greatest accessibility. */
/* You can skip this to just use the browser's defaults. */
:focus {
  box-shadow: 0 0 0 2px var(--happy-mustard), 0 0 0 4px var(--happy-mustard), 0 0 0 6px var(--deep-purple);
  border-radius: 2px;
  outline: 0;
}

.section.banner-title h1:focus {
  box-shadow: 0 0 0 4px var(--deep-purple), 0 0 0 6px var(--happy-mustard), 12px 12px 0px 0px var(--deep-purple), 0 0 0 2px var(--happy-mustard);
  border-radius: 0;
}

/* When mouse is detected, ALL focused elements have outline removed. */
body.using-mouse :focus {
  outline: none;
  box-shadow: none;
}


body.using-mouse .section.banner-title h1:focus {
  outline: none;
  box-shadow: 12px 12px 0px 0px var(--deep-purple);
}

body {
  background: var(--off-white);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main {
  overflow: hidden;
  margin-top: 5rem;
}

.section {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 1.25rem;
}

@media (min-width: 768px) {
  .product-list {
    margin-top: 2rem;
  }
  .section {
    padding: 0 2.5rem;
  }
}
  
@media (min-width: 1600px) {
  .section {
    max-width: 1440px;
  }
}

.section.loadup {
  animation: bannerLoadUp 1s forwards;
  margin-top: 2rem;
}

.PageContent {
  transition: transform 1s ease-in, opacity 0.5s ease;
}

.bg-dots {
  background: linear-gradient(135deg, var(--happy-mustard) 62%, transparent 38%), radial-gradient(var(--off-white) 4px, transparent 4px), radial-gradient(var(--off-white) 4px, transparent 4px), transparent;
  background-size: 100%, 24px 24px, 24px 24px, 100%;
  background-position: 0 0, -2px 6px, 10px 18px, 0 0;
}

.bg-stripes {
  background-image: linear-gradient(135deg, var(--off-white) 62%, transparent 38%), linear-gradient(var(--off-white), var(--off-white) 7px, var(--off-white) 7px, var(--off-white));
  background-size: 100%, 100% 14px;
}

.footer {
  border-top: 1px solid var(--deep-purple);
  padding: 1rem 0;
}

button.block, form input[type="submit"] {
  font-size: 1rem;
  color: var(--deep-purple);
  box-shadow: 12px 12px 0px 0px var(--deep-purple);
  padding: 0.25rem 1.5rem;
  background: var(--off-white);
  cursor: pointer;
  transition: 0.3s ease;
}

button.action, input.action {
  margin-bottom: 2rem;

  border-radius: 1.5rem;
  height: 2.5rem;
  min-width: 10rem;
}

button:hover, form input[type="submit"]:hover {
  /* transform: translate(1rem, 1rem); */
  box-shadow: none;
}

@keyframes fadeIn {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes blurIn {
  0%   { 
    filter: blur(1px);
    transform: scale(0.95);
   }
  100% { 
    filter: blur(0);
    transform: scale(1)
  }
}

@keyframes bannerLoadUp {
  0%   { 
      opacity: 0; 
      transform: translateY(5px);
  }
  100% { 

      transform: translateY(0);
      opacity: 1; 
  }
}